@charset "UTF-8";

@import "./common";
@import "./Grid/index";
@import "./Button/index";
@import "./Icon/index";
@import "./Nav/index";
@import "./Form/index";
@import "./Menu/index";
@import "./Group/index";
@import "./Modal/index";
@import "./A/index";
@import "./Curtain/index";
@import "./Progress/index";
@import "./Tooltip/index";

*[class^='bmbo-'], *[class*=' bmbo-'] {
	box-sizing: border-box;
	outline: none;

	&[role="button"] {
		cursor: pointer;
	}

	a {
		text-decoration: none;
		color: inherit;
	}
}

.bmbo-portals-holder {
	width: 0;
	overflow: visible;
}

.bmbo-caret {
	width: 0;
	height: 0;
	display: inline-block;

	border: 4px solid transparent;
	border-left-color: currentColor;

	transition: all var(--bmbo-duration);

	&.bmbo-caret-down {
		 transform: rotate(90deg) translate(30%, 30%);
	}
}

.bmbo-scrollbar-measure {
	position: absolute;
	top: -9999px;
	width: 50px;
	height: 50px;
	overflow: scroll;
}

.bmbo-text-cursor {
	display: inline-block;
	height: 1em;
	width: 0.3em;
	background: currentColor;
	vertical-align: text-top;

	animation: bmbo-text-cursor var(--bmbo-duration-slow) linear infinite;
}

@keyframes bmbo-text-cursor {
	0% { opacity: 1; }
	50% { opacity: 0; }
	100% { opacity: 1; }
}

.bmbo-hidden {
	opacity: 0;
	transition: none!important;
}

@mixin size($size) {
	.bmbo-#{$size} {
		font-size: var(--bmbo-#{$size});
	}
}

@include size(xs);
@include size(sm);
@include size(md);
@include size(lg);
@include size(xl);



